import Vue from 'vue'
import Router from 'vue-router'
import Dishboard from '@/components/Dishboard'
import User from '@/components/User'
import Notfound from '@/components/Notfound'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dishboard',
      component: Dishboard
    },
    {
      path: '*',
      name: 'Notfound',
      component: Notfound
    },
    {
      path: '/user/:para',
      name: 'User',
      component: User
    }
  ]
})



