<template>
  <div class="notfound">
    <Breadcrumb
      backto="Dishboard"
      submit="back"
      @submit="do_submit"
    ></Breadcrumb>
    <h1>Unknown Function</h1>
    <h2>Some thing wrong</h2>
  </div>
</template>

<script>
import Breadcrumb from './Breadcrumb'

export default {
  name: 'Notfound',
  components: {
    Breadcrumb
  },
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    do_submit (para) {
      console.log('Notfound', para)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
  padding-top: 2rem;
  font-weight: normal;
}
</style>



