<template>
  <div class="breadcrumb text-center">
    <el-row>
      <el-col :span="2">
        <i v-if="backto" class="el-icon-arrow-left" @click="backtolast"></i>
      </el-col>
      <el-col :span="20">
        {{ mytitle }}&nbsp;
      </el-col>
      <el-col :span="2">
        <i v-if="submit" class="el-icon-search" @click="sendout"></i>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: 'Breadcrumb',
  props: ['backto', 'title', 'submit'],
  data () {
    return {
      mytitle: ''
    }
  },
  methods: {
    backtolast () {
      this.$router.push({name: this.backto})
      // this.$router.push({name: this.backto, params: { para: this.submit }})
    },
    sendout () {
      this.$emit('submit', this.submit)
    }
  },
  mounted () {
    console.log('Breadcrumb mounted', this.backto, this.submit)
    if (this.title) {
      this.mytitle = this.title
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.breadcrumb {
  background: linear-gradient(to bottom, rgb(46, 145, 250) 0%, rgb(3, 60, 117) 100%);
  font-weight: normal;
  color: white;
  height: 2rem;
  font-size: 1.5rem;
  padding-top: 0.25rem;
}
</style>



